          </van-cell-group>
          <van-action-sheet v-model="sex.show" :actions="sex.list" @select="chooseSex" />

          <!-- 年龄 -->
          <van-cell-group>
            <van-field :value="s_age.name" label="年龄" disabled @click="toLink('age')" :error='s_age.error' :error-message="s_age.err_msg" right-icon="arrow"/>
