          <!-- 身高 -->
          <van-cell-group>
            <van-field v-model="$store.state.s_height.m.name" v-if="$store.state.s_sex.status == 'm'" :value="s_height.m.name" label="身高" :error='s_height.error' :error-message="s_height.err_msg" @click="toLink('height')" right-icon="arrow" disabled />
