            <van-field v-if="$store.state.s_sex.status == 'f'" :value="s_weight.f.name" label="体重"  :error='s_weight.error' :error-message="s_weight.err_msg" @click="toLink('weight')" right-icon="arrow" disabled />
          </van-cell-group>


