          <van-cell-group>
            <van-field v-model="$store.state.s_shape.m.title" v-if="$store.state.s_sex.status == 'm'" :value="s_shape.m.name" label="体型" :error='s_shape.error' :error-message="s_shape.err_msg" @click="toLink('shape')" right-icon="arrow" disabled />
            <van-field v-model="$store.state.s_shape.f.title" v-if="$store.state.s_sex.status == 'f'" :value="s_shape.f.name" label="体型" :error='s_shape.error' :error-message="s_shape.err_msg" @click="toLink('shape')" right-icon="arrow" disabled />
          </van-cell-group>
        </div>
