<script>
import { mapState, mapGetters } from "vuex";
import BackSize from '../components/BackSize.vue'
import PadSize from '../components/PadSize.vue'
import BackHard from '../components/BackHard.vue'
import PadHard from '../components/PadHard.vue'

// import api from '../api/index'
export default {
  name: 'home',
  components: {
    BackSize,
    PadSize,
    BackHard,
    PadHard
  },
  data() {
    return {
      error: false,
      val: '',
      username: { // 姓名
        val: '',
        error: false,
        err_msg: ''
      },
      sex: {
        val: {
          name: '男',
          status: 'm'
        },
        show: false,
        list: [{
          name: '男',
          status: 'm'
        },{
          name: '女',
          status: 'f'
        }]
      },
      age: {
        error: false,
        err_msg: ''
      }
      
    }
  },
  computed: {
      ...mapState({
          s_sex: state => state.s_sex,
          s_age: state => state.s_age,
          s_height: state => state.s_height,
          s_weight: state => state.s_weight,
          s_shape: state => state.s_shape,
          s_car: state => state.s_car,
          s_level: state => state.s_level,
          s_price: state => state.s_price,
          s_material: state => state.s_material
      })
  },
  created() {
    // api.tableInfo().then(res => {
    //   console.log(res)
    // })
  },
  methods: {
    submit() {
      console.log("提交！");
      let errorCount = 0;
      console.log( this.username )
      if(!this.username.val) {
        this.username.error = true;
        this.username.err_msg = '请填写姓名！'
      } else {
        this.username.error = false;
        this.username.err_msg = '';
      }

      console.log( this.s_age )
      if(!this.s_age.id) {
        console.log(1)
        this.$store.state.s_age.error = true;
        this.$store.state.s_age.err_msg = '请选择年龄';
      } else {
        this.$store.state.s_age.error = false;
        this.$store.state.s_age.err_msg = '';
      }

      console.log( this.s_sex )
      console.log( this.s_height )


      
      if(this.s_sex.status == 'm') {
        console.log("M")
        if(!this.s_height.m.id) {
          console.error("请选择身高_m")
          this.$store.state.s_height.error = true;
          this.$store.state.s_height.err_msg = '请选择身高';
        } else {
          this.$store.state.s_height.error = false;
          this.$store.state.s_height.err_msg = '';
        }

        if(!this.s_weight.m.id) {
          console.error("请选择体重_m")
          this.$store.state.s_weight.error = true;
          this.$store.state.s_weight.err_msg = '请选择体重';
        } else {
          this.$store.state.s_weight.error = false;
          this.$store.state.s_weight.err_msg = '';
        }

        if(!this.s_shape.m.id) {
          console.error("请选择体型_m")
          this.$store.state.s_shape.error = true;
          this.$store.state.s_shape.err_msg = '请选择体型';
        } else {
          this.$store.state.s_shape.error = false;
          this.$store.state.s_shape.err_msg = '';
        }
      } else {
        if(!this.s_height.f.id) {
          console.error("请选择身高_f")
          this.$store.state.s_height.error = true;
          this.$store.state.s_height.err_msg = '请选择身高';
        } else {
          this.$store.state.s_height.error = false;
          this.$store.state.s_height.err_msg = '';
        }

        if(!this.s_weight.f.id) {
          console.error("请选择体重_f")
          this.$store.state.s_weight.error = true;
          this.$store.state.s_weight.err_msg = '请选择体重';
        } else {
          this.$store.state.s_weight.error = false;
          this.$store.state.s_weight.err_msg = '';
        }
        
        if(!this.s_shape.f.id) {
          console.error("请选择体型_f")
          this.$store.state.s_shape.error = true;
          this.$store.state.s_shape.err_msg = '请选择体型';
        } else {
          this.$store.state.s_shape.error = false;
          this.$store.state.s_shape.err_msg = '';
        }
      }


      console.log( this.s_price )
      if(!this.s_price.id) {
        console.error("请选择价格")
        this.$store.state.s_price.error = true;
        this.$store.state.s_price.err_msg = '请选择体型';
      } else {
        this.$store.state.s_price.error = false;
        this.$store.state.s_price.err_msg = '';
      }


    },
    toLink(link) {
        console.log( link )
        this.$router.push(link)
    },
    chooseSex(item) {
      this.sex.show = false;
      console.log( item )
      this.sex.val = item;
      this.$store.state.s_sex = item;
    },
    chooseAge(item) {
      console.log( item )
      this.age.show = false;
      this.age.val = item.name
    },
    chooseHeight_m(item) {
      console.log( item )
      this.height.show_m = false;
      this.height.val_m = item;
    },
    chooseHeight_f(item) {
      console.log( item )
      this.height.show_f = false;
      this.height.val_f = item;
    },
    chooseWeight_m(item) {
      console.log( item )
      this.weight.show_m = false;
      this.weight.val_m = item;
    },
    chooseWeight_f(item) {
      console.log( item )
      this.weight.show_f = false;
      this.weight.val_f = item;
    },
    choosePrice_l(picker, value, index) {
      console.log(`当前值：${value}, 当前索引：${index}`);
      this.price.arr[0] = {
        val: value,
        index: index
      };
    },
    choosePrice_r(picker, value, index) {
      console.log(`当前值：${value}, 当前索引：${index}`);
      this.price.arr[1] = {
        val: value,
        index: index
      };
    },
    choosePrice() {
      console.log("确定")
      console.log( this.price.arr )
      let arr = this.price.arr;

      if(arr[0].index > arr[1].index) {
        console.log(1)
        this.price.val = arr[1].val + '-' + arr[0].val;
      } else if(arr[0].index > arr[1].index) {
        console.log(2)
        this.price.val = arr[0].val + '-' + arr[1].val;
      } else if(arr[0].index == arr[1].index) {
        console.log(3)
        this.price.val = arr[0].val;
      }
    }
  }
}
</script>
